// Navbar.js
import React from 'react';
import { Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, IconButton } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Navigate } from 'react-router-dom';


const Navbar = () => {
  const handleLogout = () => {
    // Perform logout actions here (e.g., clear local storage, reset state, etc.)
    // After performing logout actions, navigate to the login page
    return <Navigate to="/login" />;
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <IconButton
          size="large"
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{ mr: 2 }}
        >
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          Referral App
        </Typography>
        <Button color="inherit" component={Link} to="/dashboard">Dashboard</Button>
        <Button color="inherit" component={Link} to="/track-referral">Track Referral</Button>
        <Button color="inherit" component={Link} to="/generate-referral-code">Generate Referral Code</Button>
        <Button color="inherit" component={Link} to="/referral-code">Referral Code</Button>
        <Button color="inherit" component={Link} to="/login">Logout</Button>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
