import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography, Paper } from '@mui/material';

const ReferralCode = () => {
  const [referralCode, setReferralCode] = useState('');

  useEffect(() => {
    const fetchReferralCode = async () => {
      try {
        const res = await axios.get('https://referral-assessment.onrender.com/referral-code', {
          headers: {
            'x-auth-token': localStorage.getItem('token') // Assuming you're storing the token in localStorage
          }
        });
        setReferralCode(res.data.referralCode);
      } catch (err) {
        console.error(err.response.data);
      }
    };

    fetchReferralCode();
  }, []);

  return (
    <Container maxWidth="sm" style={{ marginTop: '50px' }}>
      <Typography variant="h4">Your Referral Code:</Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell component="th" scope="row">
                Referral Code:
              </TableCell>
              <TableCell>{referralCode}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Container>
  );
};

export default ReferralCode;
