import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from 'react-router-dom';
import { Container, TextField, Button, Typography, Grid } from '@mui/material';
import { styled } from '@mui/material/styles';

const FormContainer = styled('div')({
  marginTop: '50px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
});

const Register = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    referralCode: ''
  });
  const { name, email, password, referralCode } = formData;
  const navigate = useNavigate();

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    try {
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };

      const body = JSON.stringify({ name, email, password, referralCode });

      const res = await axios.post('https://referral-assessment.onrender.com/api/auth/register', body, config);
      
      console.log(res.data);
      navigate('/login');
    } catch (err) {
      console.error(err.response.data);
    }
  };

  return (
    <Container maxWidth="sm">
      <FormContainer>
        <form onSubmit={onSubmit} style={{ width: '100%' }}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography variant="h4" align="center">Register</Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth label="Name" name="name" value={name} onChange={onChange} required />
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth label="Email Address" name="email" value={email} onChange={onChange} required />
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth type="password" label="Password" name="password" value={password} onChange={onChange} minLength="6" required />
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth type="text" label="Referral Code (if any)" name="referralCode" value={referralCode} onChange={onChange} />
            </Grid>
            <Grid item xs={12}>
              <Button type="submit" variant="contained" color="primary" fullWidth>Register</Button>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1" align="center">
                Already have an account? <Link to="/login">Login here</Link>
              </Typography>
            </Grid>
          </Grid>
        </form>
      </FormContainer>
    </Container>
  );
};

export default Register;
