// src/components/Dashboard.js

import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { PieChart, Pie, Cell, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';

// Define COLORS array for pie chart colors
const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#AF19FF', '#FF1919'];

const Dashboard = () => {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const res = await axios.get('https://referral-assessment.onrender.com/referral/stats');
        setStats(res.data);
        setLoading(false);
      } catch (err) {
        console.error(err);
      }
    };

    fetchStats();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  // Data for the pie chart
  const pieChartData = [
    { name: 'Users with Referral Codes', value: stats.usersWithReferralCodes },
    { name: 'Successful Referrals', value: stats.successfulReferrals }
    // Add more data if needed
  ];

  // Data for the bar chart
  const barChartData = [
    { name: 'Users', value: stats.totalUsers },
    // Add more data if needed
  ];

  return (
    <div style={{ backgroundColor: '#f0f0f0', padding: '20px' }}>
      <div style={{ backgroundColor: '#fff', padding: '20px', borderRadius: '5px', marginBottom: '20px' }}>
        <h1>Referral Program Dashboard</h1>
        
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          {/* Pie Chart */}
          <div style={{ flex: 1 }}>
            <h2>Pie Chart</h2>
            <PieChart width={400} height={400}>
              <Pie
                data={pieChartData}
                dataKey="value"
                nameKey="name"
                cx="50%"
                cy="50%"
                outerRadius={80}
                fill="#8884d8"
                label
              >
                {pieChartData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </div>
          
          {/* Bar Chart */}
          <div style={{ flex: 1 }}>
            <h2>Bar Chart</h2>
            <BarChart width={400} height={300} data={barChartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="value" fill="#8884d8" />
            </BarChart>
          </div>
        </div>
      </div>
      
      {/* Table */}
      <div style={{ backgroundColor: '#fff', padding: '20px', borderRadius: '5px' }}>
        <h2>Table</h2>
        <TableContainer component={Paper}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>Stat</TableCell>
                <TableCell align="right">Value</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell>Total Users</TableCell>
                <TableCell align="right">{stats.totalUsers}</TableCell>
              </TableRow>
              <TableRow>
                <TableCell>Users with Referral Codes</TableCell>
                <TableCell align="right">{stats.usersWithReferralCodes}</TableCell>
              </TableRow>
              <TableRow>
                <TableCell>Successful Referrals</TableCell>
                <TableCell align="right">{stats.successfulReferrals}</TableCell>
              </TableRow>
              {/* Add more rows if needed */}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </div>
  );
};

export default Dashboard;
