import React, { useState } from 'react';
import axios from 'axios';
import { TextField, Button, Typography, Container } from '@mui/material';

const ReferralTracking = () => {
  const [formData, setFormData] = useState({
    referralCode: ''
  });
  const [rewardAmount, setRewardAmount] = useState(null);

  const { referralCode } = formData;

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    try {
      const config = {
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': localStorage.getItem('token')
        }
      };

      const body = JSON.stringify({ referralCode });

      const res = await axios.post('https://referral-assessment.onrender.com/api/referral/track', body, config);

      setRewardAmount(res.data.rewardAmount);
    } catch (err) {
      console.error(err.response.data);
    }
  };

  return (
    <Container maxWidth="sm">
      <Typography variant="h4" gutterBottom>Referral Tracking</Typography>
      <form onSubmit={onSubmit}>
        <TextField
          type="text"
          placeholder="Referral Code"
          name="referralCode"
          value={referralCode}
          onChange={onChange}
          variant="outlined"
          fullWidth
          margin="normal"
          required
        />
        <Button type="submit" variant="contained" color="primary">Track Referral</Button>
      </form>
      {rewardAmount !== null && (
        <div>
          <Typography variant="h6" gutterBottom>Reward Amount:</Typography>
          <Typography variant="body1">{rewardAmount}</Typography>
        </div>
      )}
    </Container>
  );
};

export default ReferralTracking;
