import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from 'react-router-dom'; // Import useNavigate and Link
import { Container, TextField, Button, Typography, Grid } from '@mui/material';
import { styled } from '@mui/material/styles';

const FormContainer = styled('div')({
  marginTop: '50px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
});

const Login = () => {
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    referralCode: ''
  });
  const { email, password, referralCode } = formData;
  const navigate = useNavigate();

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    try {
      const config = {
        headers: {
          'Content-Type': 'application/json'
        }
      };

      const body = JSON.stringify({ email, password, referralCode });

      const res = await axios.post('https://referral-assessment.onrender.com/api/auth/login', body, config);
      localStorage.setItem('token', res.data.token);

      // Check if the referralCode is provided
      if (referralCode) {
        // If referralCode is provided, navigate to the dashboard
        navigate('/dashboard');
      } else {
        // If referralCode is not provided, navigate to the register page
        navigate('/register');
      }
    } catch (err) {
      console.error(err.response.data);
    }
  };

  return (
    <Container maxWidth="sm">
      <FormContainer>
        <form onSubmit={onSubmit} style={{ width: '100%' }}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Typography variant="h4" align="center">Login</Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth label="Email Address" name="email" value={email} onChange={onChange} required />
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth type="password" label="Password" name="password" value={password} onChange={onChange} minLength="6" required />
            </Grid>
            <Grid item xs={12}>
              <TextField fullWidth type="text" label="Referral Code (if any)" name="referralCode" value={referralCode} onChange={onChange} />
            </Grid>
            <Grid item xs={12}>
              <Button type="submit" variant="contained" color="primary" fullWidth>Login</Button>
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1" align="center">
                Don't have an account? <Link to="/register">Register here</Link>
              </Typography>
            </Grid>
          </Grid>
        </form>
      </FormContainer>
    </Container>
  );
};

export default Login;
