import React, { useState } from 'react';
import axios from 'axios';
import { Button, Typography, Container } from '@mui/material';

const GenerateReferralCode = () => {
    const [referralCode, setReferralCode] = useState('');

    const generateCode = async () => {
        try {
            const res = await axios.post('https://referral-assessment.onrender.com/generate');
            setReferralCode(res.data.referralCode);
        } catch (err) {
            console.error(err.response.data);
        }
    };

    return (
        <Container maxWidth="sm">
            <Typography variant="h4" gutterBottom>Generate Referral Code</Typography>
            <Button variant="contained" color="primary" onClick={generateCode}>Generate Code</Button>
            {referralCode && <Typography variant="body1">Referral Code: {referralCode}</Typography>}
        </Container>
    );
};

export default GenerateReferralCode;
