// App.js

import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Navbar from './Navbar';
import Login from './components/Login';
import Register from './components/Register';
import Dashboard from './components/Dashboard';
import ReferralTracking from './components/ReferralTracking';
import GenerateReferralCode from './components/ReferralCodeGeneration';
import ReferralCode from './components/ReferralCode';

const App = () => {
  return (
    <Router> {/* Wrap your entire app within the Router component */}
      <div>
        <Navbar /> {/* Always render Navbar */}
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="/dashboard" element={<Dashboard />} />
          <Route path="/track-referral" element={<ReferralTracking />} />
          <Route path="/generate-referral-code" element={<GenerateReferralCode />} />
          <Route path="/referral-code" element={<ReferralCode />} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;
